import mouseAction from '../../utils/globalObject';
import drawPixel from '../../utils/drawPixel';
import getPixelNumber from '../../utils/getPixelNumber';

// 
function strokeToolDraw(e) {
  const pixelArray = mouseAction.canvasArray.canvasUnits;
  if (mouseAction.toolButton === 'draw-stroke') {
    //   mouseAction       ,
    //      "",
    //       "",    scale.
    //   "",   ,   .
    let x1 = pixelArray[getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY)][5];
    const x2 = pixelArray[getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY)][5];
    let y1 = pixelArray[getPixelNumber(pixelArray, mouseAction.startX, mouseAction.startY)][6];
    const y2 = pixelArray[getPixelNumber(pixelArray, mouseAction.endX, mouseAction.endY)][6];
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const signX = x1 < x2 ? 1 : -1;
    const signY = y1 < y2 ? 1 : -1;
    let pixelNumber;
    let error = deltaX - deltaY;
    for (let i = 0; i < pixelArray.length; i += 1) {
      if (pixelArray[i][5] === x2 && pixelArray[i][6] === y2) { pixelNumber = i; }
    }
    // ,     ko "",   
    const pixelsOnLine = [pixelNumber];
    while (x1 !== x2 || y1 !== y2) {
      const error2 = error * 2;
      if (error2 > -deltaY) {
        error -= deltaY;
        x1 += signX;
      }
      if (error2 < deltaX) {
        error += deltaX;
        y1 += signY;
      }
      //    ""  pixelArray,
      //       ""
      for (let i = 0; i < pixelArray.length; i += 1) {
        if (pixelArray[i][5] === x1 && pixelArray[i][6] === y1) { pixelNumber = i; }
      }
      if (!pixelsOnLine.includes(pixelNumber)) {
        pixelsOnLine.push(pixelNumber);
      }
    }
    //  ,    "",    .
    if (e.button === 0) {
      pixelsOnLine.forEach((item) => {
        drawPixel(pixelArray, item, mouseAction.primaryColor, document.querySelector('.draw-area'));
      });
    } else if (e.button === 2) {
      pixelsOnLine.forEach((item) => {
        drawPixel(pixelArray, item, mouseAction.secondaryColor, document.querySelector('.draw-area'));
      });
    }
  }
}

export default strokeToolDraw;
